.placer {
  display: flex;
}

.box-main {
  flex-direction: row;
  margin: 20px;
  border-left: 5px solid rgb(200, 223, 219);
  border-top: 5px solid rgb(200, 223, 219);
  max-width: 25%;
  padding: 20px;
}

.brand {
  color: lightcyan;
  margin: 20px 0 10px 20px;
}

.heading {
  text-decoration: none;
  min-height: 50px;
  line-height: 3.5rem;
  color: rgb(200, 223, 219);
  padding: 20px;
}

.content {
  padding: 20px 20px 0 20px;
  color: rgb(200, 223, 219);
  margin: 0;
}

@media only screen and (max-width: 700px) {

    .placer{
      display: inline;
    }
    .box-main {
        max-width: 100%;
        flex-direction: column; 
    }

    img {
      max-width: 85%;
    }
  }
